.container {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
}
@font-face {
  font-family: "Oswald";
  src: url("..//fonts//oswaldmedium.woff2"), url("..//fonts/oswaldmedium.woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("..//fonts/oswaldregular.woff2"), url("..//fonts/oswaldregular.woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  background-color: #ffffff;
}
* {
  box-sizing: inherit;
}
a {
  text-decoration: none;
  color: #000000;
}
img {
  width: 100%;
  height: auto;
}
input[type="number"] {
  appearance: textfield;
}
.title {
  margin-right: 0;
  font-size: 36px;
  line-height: 1;
  color: #111111;
  font-weight: 400;
}
.title--green-line {
  width: 100%;
  font-size: 24px;
  position: relative;
  padding-left: 0;
}
.title--green-line::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 0.59em;
  border-bottom: 3px solid #68b738;
}
.title__inner {
  display: inline-block;
  padding: 0 24px 0 20px;
  position: relative;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .title--green-line {
    font-size: 36px;
    padding-left: 49px;
  }
  .title__inner {
    padding-left: 40px;
  }
  .title__inner--catalog {
    padding-left: 18px;
  }
}
@media (min-width: 1300px) {
  .title {
    font-size: 60px;
    line-height: 1;
  }
  .title--green-line {
    font-size: 36px;
    padding-left: calc(50% - 650px);
    position: relative;
  }
  .title__inner {
    padding-left: 39px;
  }
}
.button {
  display: block;
  min-width: 245px;
  min-height: 33px;
  margin: 0 auto;
  padding-top: 10px;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: "Oswald", "Open Sans", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.main-nav {
  border-bottom: 1px solid #eaeaea;
}
.main-nav__logo-wrapper {
  position: relative;
  display: inline-block;
  width: 191px;
}
.main-nav__logo-wrapper:hover {
  opacity: 0.8;
}
.main-nav__logo-wrapper:active {
  opacity: 0.6;
}
.main-nav__container {
  width: 280px;
  margin: 0 auto;
  padding-top: 12px;
  padding-bottom: 12px;
}
.main-nav__toggle {
  display: none;
  position: absolute;
  top: 24px;
  right: 16px;
  width: 28px;
  height: 18px;
  border: none;
  background: none;
  background-repeat: no-repeat;
  background-image: url("../img/icon-mobile-menu.svg");
  outline: none;
}
.main-nav__toggle--close {
  width: 25px;
  height: 18px;
  background-image: url("../img/icon-close.svg");
}
@keyframes menu__falling {
  0% {
    transform: translateY(-200px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.main-nav__toggle--open {
  display: block;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__list--close {
  display: none;
}
.main-nav__list--open {
  display: block;
  animation: menu__falling 0.6s;
}
.main-nav__item {
  min-height: 67px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid #eaeaea;
  text-align: center;
}
.main-nav__item:first-of-type {
  border-top: 1px solid #eaeaea;
}
.main-nav__link {
  display: block;
  margin-top: 18px;
  font-size: 20px;
  line-height: 1;
  color: #111111;
  font-weight: 500;
  text-transform: uppercase;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link:focus {
  color: rgba(17, 17, 17, 0.3);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    margin-right: 4%;
    margin-left: 4%;
    border: none;
  }
  .main-nav__logo-wrapper {
    width: 172px;
    height: 60px;
    margin-left: -18px;
  }
  .main-nav__container {
    width: auto;
    margin-top: 24px;
    padding: 0;
  }
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    width: 73%;
  }
  .main-nav__list--close {
    animation: none;
  }
  .main-nav__item {
    width: auto;
    margin-left: 6.8%;
    border: none;
  }
  .main-nav__item:first-of-type {
    border: none;
  }
  .main-nav__link {
    margin-top: 25px;
    font-size: 18px;
    line-height: 1.39;
    color: #222222;
    font-weight: normal;
  }
  .main-nav__link--active::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    margin-top: 6px;
    background-color: #68b738;
  }
}
@media (min-width: 1300px) {
  .main-nav {
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 40px;
    z-index: 2;
    justify-content: space-between;
    padding-top: 32px;
  }
  .main-nav__logo-wrapper {
    width: 202px;
    margin: 0;
  }
  .main-nav__container {
    margin: 0;
    margin-top: 21px;
  }
  .main-nav__list {
    justify-content: flex-end;
    margin: 0;
  }
  .main-nav__item {
    margin-left: 35px;
    border: none;
  }
  .main-nav__link {
    font-size: 20px;
    line-height: 1.25;
    font-weight: 400;
    color: #222222;
  }
  .main-nav__link--index {
    color: #ffffff;
  }
  .main-nav__link--index:first-of-type::after {
    background-color: #ffffff;
  }
  .main-nav__link--index:focus {
    color: rgba(255, 255, 255, 0.6);
  }
}
