.programs {
  min-width: 320px;
  margin-top: 12px;
  margin-bottom: 29px;
}
.programs__list {
  min-width: 280px;
  margin: 0;
  padding-right: 20px;
  padding-left: 20px;
  list-style: none;
}
.programs__item {
  position: relative;
  margin-bottom: 20px;
  padding: 24px 20px 19px 19px;
  border-top: 1px solid transparent;
  background-color: #f2f2f2;
}
.programs__icon {
  position: relative;
  float: left;
  width: 50px;
  height: 50px;
  margin-left: 3px;
  border-radius: 50%;
  background-color: #68b738;
}
.programs__icon--slim::before {
  content: "";
  position: absolute;
  left: calc(50% - 70% / 2);
  top: -6%;
  display: block;
  width: 70%;
  height: 98%;
  background-image: url("..//img/icon-cat-slim.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.programs__icon--gaining::before {
  content: "";
  position: absolute;
  top: calc(50% - 56% / 2);
  left: calc(50% - 134% / 2);
  display: block;
  width: 134%;
  height: 56%;
  background-image: url("..//img/icon-cat-pro.svg");
  background-repeat: no-repeat;
}
.programs__title {
  margin: 0;
  margin-top: 5px;
  margin-left: 72px;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  font-weight: 400;
  text-align: left;
  color: #111111;
}
.programs__description {
  position: relative;
  margin: 0;
  margin-left: 1px;
  margin-bottom: 40px;
  padding-top: 29px;
  line-height: 18px;
  font-family: "Arial", "Helvetica", sans-serif;
  text-align: left;
  color: #444444;
}
.programs__description::after {
  content: "";
  position: absolute;
  bottom: -23px;
  left: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #000000;
  opacity: 0.1;
}
.programs__link {
  position: relative;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #111111;
}
.programs__link:hover::after {
  width: 35px;
  background-image: url("..//img/icon-right-arrow-verylong.svg");
  background-position: 0 0;
}
.programs__link:active {
  color: rgba(17, 17, 17, 0.3);
}
.programs__link:active::after {
  opacity: 0.3;
}
.programs__link:focus::after {
  background-image: url("..//img/icon-right-arrow-verylong.svg");
}
.programs__link::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 98px;
  display: block;
  width: 25px;
  height: 12px;
  background-image: url("..//img/icon-right-arrow-long.svg");
  background-position: 10% 10%;
}
