.examples {
  position: relative;
  min-width: 320px;
  background-color: #f2f2f2;
}
.examples__title {
  margin: 0;
  padding-top: 29px;
  margin-bottom: 35px;
  padding-left: 6.25%;
}
.examples__text {
  width: fit-content;
